$white: #fff;
$black: #000;
$gray: #828282;
$gray2: #605f5f;
$gray3: #464545;
$gray4: #939393;

$green: #94c34a;
$green2: #78a92b;
$green3: #77a432;
$green4: #5e8d14;
$green-d: #40630a;
$blue: #45515f;


body {
	background: #FFF;
	line-height: 1.2;
	font-size: 18px;
	font-family: 'Roboto', sans-serif;
	font-weight: 300;
	@include lg {
		font-size: 16px; } }

a {
	text-decoration: none; }
strong {
	font-weight: 700; }
.out {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	@include md {
		display: block; }
	.content {
		flex-grow: 1; }
	.content, .header, .footer {
		width: 100%; }

	.header, .footer {
		flex-grow: 0;
		flex-shrink: 0; } }
.title-lg {
	color: $white;
	text-align: center;
	font-family: Roboto;
	font-size: 48px;
	@include md {
		font-size: 25px; } }
.title-lg, .title-decor, .title-md {
	font-weight: 700; }
.title-md {
	color: $gray4;
	font-size: 30px;
	margin-bottom: 30px;
	@include md {
		font-size: 20px; }
	&--green {
		color: $green; } }
.title-m {
	color: $gray4;
	font-size: 24px;
	margin-bottom: 25px;
	@include md {
		font-size: 18px; }
	&--big-mb {
		margin-bottom: 36px;
		@include sm {
			margin-bottom: 25px; } } }

.title-decor {
	margin-bottom: 40px;
	font-size: 30px;
	color: $gray;
	position: relative;
	@include md {
		font-size: 20px; }
	&:after {
		content: "";
		position: absolute;
		bottom: -6px;
		left: 0;
		width: 51px;
		height: 1px;
		background-color: $gray3; }
	&--green {
		color: $green4;
		&:after {
			background-color: $green4; } } }

.article {
	p {
		margin-bottom: 18px;
		&:last-of-type {
			margin-bottom: 0; } }
	h1 {
		margin-bottom: 40px;
		font-size: 30px;
		color: $gray;
		position: relative;
		text-transform: uppercase;
		@include md {
			font-size: 20px; }
		&:after {
			content: "";
			position: absolute;
			bottom: -6px;
			left: 0;
			width: 51px;
			height: 1px;
			background-color: $gray3; } }
	h2 {
		color: $gray4;
		font-size: 30px;
		margin-bottom: 30px;
		@include md {
			font-size: 20px; } }
	h3 {
		color: $gray4;
		font-size: 24px;
		margin-bottom: 25px;
		@include md {
			font-size: 18px; } }
	h4 {
		color: $gray4;
		font-size: 20px;
		margin-bottom: 10px;
		@include md {
			font-size: 18px; } }
	a {
		color: $black;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			left: 1px;
			right: 1px;
			bottom: 0px;
			height: 1px;
			background: $black;
			transition: opacity 0.3s; }
		@include mm {
			&:hover {
				&:after {
					opacity: 0; } } } }
	ul, ol {
		margin-bottom: 18px; }
	ol {

		li {
			margin-bottom: 17px;
			&:last-of-type {
				margin-bottom: 0; } } } }
.list, .article ul {
	li {
		position: relative;
		padding-left: 21px;
		margin-bottom: 17px;
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 6px;
			width: 8px;
			height: 8px;
			background-color: $gray;
			border-radius: 50%; }
		&:last-of-type {
			margin-bottom: 0; } } }
.decor-block {
	position: relative;
	padding-left: 18px;
	font-size: 20px;
	margin-bottom: 61px;
	@include lg {
		font-size: 16px; }
	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 3px;
		bottom: 3px;
		width: 4px;
		background-color: $gray; }
	&--sm {
		font-size: 18px; } }
.link {
	display: inline-block;
	vertical-align: top;
	color: $green;
	font-size: 20px;
	position: relative;
	line-height: 1.1;
	&:after {
		content: '';
		position: absolute;
		left: 1px;
		right: 1px;
		bottom: 0px;
		height: 1px;
		background: $green;
		transition: opacity 0.3s; }
	@include mm {
		&:hover {
			&:after {
				opacity: 0; } } }
	&--dark {
		color: $black;
		&:after {
			background: $black; } } }
